import { useGetJournalEntriesQuery, useCreateJournalMutation } from './journal-data-access'
import { useMemo, useState } from 'react'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '../ui/table'
import { Button } from '../ui/button'
import { RefreshCw } from 'lucide-react'
import { Address } from 'gill'

export function JournalList({ address }: { address: Address }) {
  const query = useGetJournalEntriesQuery({ address })
  const [showAll, setShowAll] = useState(false)

  const items = useMemo(() => {
    if (showAll) return query.data
    return query.data?.slice(0, 5)
  }, [query.data, showAll])

  return (
    <div className="space-y-2">
      <div className="flex justify-between">
        <h2 className="text-2xl font-bold">Transaction History</h2>
        <div className="space-x-2">
          {query.isLoading ? (
            <span className="loading loading-spinner"></span>
          ) : (
            <Button variant="outline" onClick={() => query.refetch()}>
              <RefreshCw size={16} />
            </Button>
          )}
        </div>
      </div>
      {query.isError && <pre className="alert alert-error">Error: {query.error?.message.toString()}</pre>}
      {query.isSuccess && (
        <div>
          {query.data.length === 0 ? (
            <div>No journals found.</div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Title</TableHead>
                  <TableHead>Content</TableHead>
                  <TableHead>Operation</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {items?.map((item) => (
                  <TableRow key={item.data.title}>
                    <TableCell className="font-mono">{item.data.title}</TableCell>
                    <TableCell>{item.data.content}</TableCell>
                    <TableCell>
                      <button
                        className="bg-yellow-500 text-white px-2 py-1 mr-2"
                        onClick={() => console.log(`Edit journal: ${item.data.title}`)}
                      >
                        Update
                      </button>
                      <button
                        className="bg-red-500 text-white px-2 py-1"
                        onClick={() => console.log(`Delete journal: ${item.data.title}`)}
                      >
                        Delete
                      </button>
                    </TableCell>
                  </TableRow>
                ))}
                {(query.data?.length ?? 0) > 5 && (
                  <TableRow>
                    <TableCell colSpan={4} className="text-center">
                      <Button variant="outline" onClick={() => setShowAll(!showAll)}>
                        {showAll ? 'Show Less' : 'Show All'}
                      </Button>
                    </TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          )}
        </div>
      )}
    </div>
  )
}

export function CreateJournal({ address }: { address: Address }) {
  const [title, setTitle] = useState('')
  const [content, setContent] = useState('')
  const [loading, setLoading] = useState(false)
  const mutation = useCreateJournalMutation({ address })

  const handleSubmit = async () => {
    setLoading(true)
    console.log(`Poll ID: ${title}, Choice: ${content}`)
    await mutation.mutateAsync(
      {
        title,
        content,
      },
      {
        onSuccess: () => {
          setTitle('')
          setContent('')
          setLoading(false)
        },
        onError: () => {
          setLoading(false)
        },
      },
    )
  }

  return (
    <div className="max-w-xl mx-auto py-10">
      <h1 className="text-2xl font-bold mb-4">Create Journal</h1>

      <div className="mb-4">
        <label className="block font-medium mb-1">Title</label>
        <input
          type="text"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          className="w-full border rounded p-2"
        />
      </div>

      <div className="mb-4">
        <label className="block font-medium mb-1">Content</label>
        <textarea
          value={content}
          onChange={(e) => setContent(e.target.value)}
          className="w-full border rounded p-2"
          rows={6}
        />
      </div>

      <button
        onClick={handleSubmit}
        disabled={loading}
        className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
      >
        {loading ? 'Submitting...' : 'Create Journal'}
      </button>
    </div>
  )
}
