'use client'

import {
  CURD_DAPP_PROGRAM_ADDRESS,
  decodeJournalEntry,
  getCreateJournalEntryInstructionAsync,
  JOURNAL_ENTRY_DISCRIMINATOR,
} from '@/../clients/ts'
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query'
import type { Address, Base64EncodedBytes, Instruction } from '@solana/kit'
import { useWalletUi } from '@wallet-ui/react'
import {
  AccountRole,
  createTransaction,
  getBase58Decoder,
  signAndSendTransactionMessageWithSigners,
  SolanaClient,
} from 'gill'
import { toastTx } from '../toast-tx'
import { toast } from 'sonner'
import { useWalletUiSigner } from '../solana/use-wallet-ui-signer'
import { PublicKey } from '@solana/web3.js'
import idl from '@/../idl/curd_dapp.json'

export async function fetchJournalEntriesByOwner(rpc: SolanaClient['rpc'], address: Address) {
  const accounts = await rpc
    .getProgramAccounts(CURD_DAPP_PROGRAM_ADDRESS, {
      encoding: 'base64',
      commitment: 'confirmed',
      filters: [
        {
          memcmp: {
            encoding: 'base64',
            offset: BigInt(0),
            bytes: Buffer.from(JOURNAL_ENTRY_DISCRIMINATOR).toString('base64') as Base64EncodedBytes,
          },
        },
        {
          memcmp: {
            encoding: 'base64',
            offset: BigInt(8),
            bytes: Buffer.from(address).toString('base64') as Base64EncodedBytes,
          },
        },
      ],
    })
    .send()

  // EncodedAccount<string>
  const encodedAccounts = accounts.map((acc) => ({
    address: acc.pubkey as Address,
    data: Buffer.from(acc.account.data[0], 'base64'),
    lamports: acc.account.lamports,
    executable: acc.account.executable,
    programAddress: CURD_DAPP_PROGRAM_ADDRESS,
    space: BigInt(acc.account.data[0].length),
  }))

  return encodedAccounts.map((ea) => decodeJournalEntry(ea))
}

function useGetJournalEntriesQueryKey({ address }: { address: Address }) {
  const { cluster } = useWalletUi()
  return ['get-journal-entries', { cluster, address }]
}

function useInvalidateGetJournalEntriesQuery({ address }: { address: Address }) {
  const queryClient = useQueryClient()
  const queryKey = useGetJournalEntriesQueryKey({ address })
  return async () => {
    await queryClient.invalidateQueries({ queryKey })
  }
}

export function useGetJournalEntriesQuery({ address }: { address: Address }) {
  const { client } = useWalletUi()

  return useQuery({
    queryKey: useGetJournalEntriesQueryKey({ address }),
    queryFn: () => fetchJournalEntriesByOwner(client.rpc, address),
  })
}

export function useCreateJournalMutation({ address }: { address: Address }) {
  const { client } = useWalletUi()
  const signer = useWalletUiSigner()
  const invalidateGetJournalEntriesQuery = useInvalidateGetJournalEntriesQuery({ address })

  return useMutation({
    mutationFn: async (input: { title: string; content: string }) => {
      try {
        // const ix = await getCreateJournalEntryInstructionAsync({
        //   signer: signer,
        //   title: input.title,
        //   content: input.content,
        // })

        const ix = buildCreateJournalEntryIx({
          title: input.title,
          content: input.content,
          signer: new PublicKey(address), // signer,
        })

        // get the leatest blockhash
        const { value: latestBlockhash } = await client.rpc.getLatestBlockhash({ commitment: 'confirmed' }).send()
        const transaction = createTransaction({
          feePayer: signer,
          version: 0,
          latestBlockhash,
          instructions: [ix],
        })

        const signatureBytes = await signAndSendTransactionMessageWithSigners(transaction)
        const signature = getBase58Decoder().decode(signatureBytes)

        console.log('create journal tx signature:', signature)
        return signature
      } catch (error: unknown) {
        console.log('error', `Transaction failed! ${error}`)
        return
      }
    },
    onSuccess: async (tx) => {
      toastTx(tx)
      await Promise.all([invalidateGetJournalEntriesQuery()])
    },
    onError: (error) => {
      toast.error(`Transaction failed! ${error.message}`)
    },
  })
}

export function buildCreateJournalEntryIx(input: { title: string; content: string; signer: PublicKey }): Instruction {
  // 1  PDA
  const [journalEntryPda] = PublicKey.findProgramAddressSync(
    [Buffer.from(input.title, 'utf8'), input.signer.toBuffer()],
    new PublicKey(CURD_DAPP_PROGRAM_ADDRESS),
  )

  // 2  discriminator
  type JournalInstruction = { name: string; discriminator: number[] }
  const ixDef = idl.instructions.find((i: JournalInstruction) => i.name === 'create_journal_entry')
  if (!ixDef) throw new Error('create_journal_entry not found in IDL')

  const discriminator = Buffer.from(ixDef.discriminator)

  // 3  title, content (anchor string: u32-len + utf8-bytes)
  const encodeString = (str: string) => {
    const buf = Buffer.from(str, 'utf8')
    const len = Buffer.alloc(4)
    len.writeUInt32LE(buf.length, 0)
    return Buffer.concat([len, buf])
  }

  const titleBuf = encodeString(input.title)
  const contentBuf = encodeString(input.content)

  // 4  data
  const data = Buffer.concat([discriminator, titleBuf, contentBuf])

  // 5  Instruction
  const ix: Instruction = {
    programAddress: CURD_DAPP_PROGRAM_ADDRESS,
    accounts: [
      { address: journalEntryPda.toBase58() as Address, role: AccountRole.WRITABLE },
      { address: input.signer.toBase58() as Address, role: AccountRole.WRITABLE_SIGNER },
    ],
    data,
  }

  return ix
}
